.theme-recursos {
  --brand-hue: 167;
  --neutral-hue: 188;
  --color-saturation: 45%;
  --color-luminosity-1: 63%;
  --color-luminosity-2: 63%;
  --color-luminosity-3: 63%;
  --color-luminosity-4: 63%;
  --color-luminosity-5: 63%;
}

.theme-recursos {
  /* color */
  --secondary-hue: 168;
  /* color tokens */
  --med-color-secondary-1: var(--secondary-hue), calc(var(--color-saturation) + var(--saturation-fator, 0%)), calc(var(--color-luminosity-mutable-1, 0%) + var(--color-luminosity-1));
  --med-color-secondary-2: var(--secondary-hue), calc(var(--color-saturation) + var(--saturation-fator, 0%)), calc(var(--color-luminosity-mutable-1, 0%) + var(--color-luminosity-2));
  --med-color-secondary-3: var(--secondary-hue), calc(var(--color-saturation) + var(--saturation-fator, 0%)), calc(var(--color-luminosity-mutable-3, 0%) + var(--color-luminosity-3));
  --med-color-secondary-4: var(--secondary-hue), calc(var(--color-saturation) + var(--saturation-fator, 0%)), calc(var(--color-luminosity-mutable-2, 0%) + var(--color-luminosity-4));
  --med-color-secondary-5: var(--secondary-hue), calc(var(--color-saturation) + var(--saturation-fator, 0%)), calc(var(--color-luminosity-mutable-2, 0%) + var(--color-luminosity-5));
  --med-color-secondary-gradient: linear-gradient(to right, hsl(var(--med-color-provas-3)), hsl(var(--med-color-provas-1)));
}

.med-color-secondary {
  --med-color-1: var(--med-color-secondary-1) !important;
  --med-color-2: var(--med-color-secondary-2) !important;
  --med-color-3: var(--med-color-secondary-3) !important;
  --med-color-4: var(--med-color-secondary-4) !important;
  --med-color-5: var(--med-color-secondary-5) !important;
  --med-color-gradient: var(--med-color-secondary-gradient) !important;
}

